General reset and box-sizing
*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body, html {
    height: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
}

/* Ensure the root element and main container take up full height */
#root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.app-container {
    display: flex;
    flex-direction: column;
    flex: 1;
}

/* Main content should expand to take up available space */
main {
    flex: 1;
    background-color: white; /* White background for the main content */
}

/* Style for the header */
header {
    background-color: whitesmoke;
    color: grey;
    padding: 2.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header h1 {
    margin: 0;
}

header nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

header nav ul li {
    margin-left: 1rem;
}

header nav ul li a {
    color: grey;
    text-decoration: none;
}

/* Form and button styles */
form {
    max-width: 500px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    background-color: #f8f8f8;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

form input, form textarea, form button {
    margin-bottom: 1rem;
    padding: 0.75rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
}

form input:focus, form textarea:focus {
    border-color: orange;
    outline: none;
    box-shadow: 0 0 5px orange;
}

form button {
    background-color: blue;
    color: white;
    border: none;
    cursor: pointer;
    padding: 0.75rem;
    font-size: 1rem;
    border-radius: 4px;
    transition: background-color 0.3s ease;
}

form button:hover {
    background-color: darkorange;
}

/* List styles for questions and answers */
.questions-container ul, .question-detail-container ul {
    list-style: none;
    padding: 0;
}

.questions-container li, .question-detail-container li {
    padding: 1rem;
    border-bottom: 1px solid #ccc;
}

.questions-container li a, .question-detail-container li a {
    text-decoration: none;
    color: blue;
}

.questions-container li a:hover, .question-detail-container li a:hover {
    text-decoration: underline;
}

/* Footer styles */
footer {
    background-color: #333;
    color: white;
    text-align: center;
    padding: 1rem;
    width: 100%;
    position: fixed;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.footer-logo-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.footer-logo {
    height: 30px; /* Adjust the height as needed */
    margin-bottom: 0.5rem;
    margin-left: 7rem;
}

.social-media-container {
    display: flex;
    gap: 0.5rem;
    margin-top: 1rem; /* Add margin to create space between the logo and social icons */
    margin-left: 10rem;
}

.social-media-icon {
    color: white;
    font-size: 30px; /* Adjust the size as needed */
}

.footer-columns {
    display: flex;
    justify-content: space-between;
    flex: 1;
}

.footer-column {
    flex: 1;
    padding: 0 12rem;
    text-align: left; /* Align text to the left */
}

.footer-column h4 {
    margin-bottom: 0.5rem;
}

.footer-column ul {
    list-style: none;
    padding: 0;
}

.footer-column ul li {
    margin-bottom: 0.5rem;
}

.footer-column ul li a {
    color: white;
    text-decoration: none;
}

.footer-column ul li a:hover {
    text-decoration: underline;
}

/* Responsive styles */
@media (max-width: 768px) {
    .footer-content {
        flex-direction: column;
        align-items: center;
    }

    .footer-columns {
        flex-direction: column;
        align-items: center;
    }

    .footer-column {
        padding: 1rem 0;
        text-align: center;
    }

    .social-media-container {
        justify-content: center;
    }
}

/* Container styles for different sections */
.login-container, .signup-container, .ask-question-container, .question-detail-container {
    max-width: 600px;
    margin: 0 auto;
}
